import React from 'react';
import styled from 'styled-components';
import { motion } from 'framer-motion';
import {
  Section,
  Title,
  CourseList,
  CourseCard,
  CourseTitle,
  Description,
  SectionCatalog,
  InlineBlocks,
  ImgSection,
  FleetInfo,
  FleetTitle,
  CenteredContainer,
} from './styles/styles';

const Courses = () => {
  const data = [
    {
      title: ' ',
      desc: ' ,  ,   .',
    },
    {
      title: ' ',
      desc: ' ,  ,  .',
    },
    {
      title: '',
      desc: ' +       -.',
    },
  ];

  const yachts = [
    {
      img: '/assets/images/large/trsm/Fleet1.webp',
      fleet: ' 520',
      info: '   12  ,   .',
    },
    {
      img: '/assets/images/large/trsm/Fleet2.webp',
      fleet: ' 50',
      info: ' 50-   2    10  ',
    },
    {
      img: '/assets/images/large/trsm/Fleet3.webp',
      fleet: ' 40',
      info: '4     ,   8-10 ',
    },
  ];

  return (
    <>
      <Section>
        <div className="container-medium">
          <Title>+ </Title>
          <CourseList>
            {data.map(({ title, desc }, index) => (
              <CourseCard
                key={title}
                initial={{ opacity: 0, y: 30 }}
                whileInView={{ opacity: 1, y: 0 }}
                viewport={{ once: true }}
                transition={{ duration: 0.5, delay: index * 0.2 }}>
                <CourseTitle>{title}</CourseTitle>
                <Description>{desc}</Description>
              </CourseCard>
            ))}
          </CourseList>
        </div>
      </Section>

      <Section $fleet>
        <div className="container-medium">
          <Title>+ </Title>
          <SectionCatalog>
            {yachts.map(({ img, fleet, info }, index) => (
              <InlineBlocks key={index}>
                <ImgSection>
                  <img src={`${process.env.PUBLIC_URL}${img}`}></img>
                </ImgSection>
                <CenteredContainer>
                  <FleetInfo>{fleet}</FleetInfo>
                  <FleetTitle>{info}</FleetTitle>
                </CenteredContainer>
              </InlineBlocks>
            ))}
          </SectionCatalog>
        </div>
      </Section>
    </>
  );
};

export default Courses;
